<template>
  <div class="starter">
    <div class="internal--level">
      <div class="flex justify-between">
        <BackButton />
        <h1 class="text-3xl font-extrabold">Hello level</h1>
        <p style="width: 60px"></p>
      </div>

      <div v-for="item in currModule.cats">
        <h2 class="h2--divider">{{ item.name }}</h2>
        <div class="flex flex-wrap justify-center">
          <div class="lessoncard--comb" v-for="act in item.activities">
            <p>{{ act.name }}</p>
            <div
              v-if="act.img"
              class="card--pic"
              @click="route(act)"
              :style="{
                'background-color': act.bgColor ?? 'white',
                'background-image': 'url(' + act.img + ')',
              }"
            >
              <div
                class="over"
                v-if="getWaterMark(act)"
                :style="{
                  'background-image': 'url(' + getWaterMark(act) + ')',
                }"
              ></div>
            </div>
            <div v-else class="card--pic" @click="route(act)"></div>
          </div>
        </div>
      </div>

     

      <!-- 
      <RouterView></RouterView> -->
    </div>
    <div v-if="isShowDialog" class="modal-bg bg-indigo-400"></div>
    <div v-if="isShowDialog" class="modal">
      <div class="modal--content">
        <button id="x" @click="isShowDialog = false">X</button>
        <!-- <h2 class="h2--divider">Choose activity</h2> -->
        <h3 class="text-3xl font-extrabold">Choose activity</h3>
        <div class="flex flex-wrap justify-center">
          <div
            class="lessoncard--simple choose--activity"
            @click="
              () => {
                $router.push('/simpleDict/' + currDict);
              }
            "
            style="background-image: url('/img/activitytype/flashcard.png')"
          />
          <div
            class="lessoncard--simple choose--activity"
            @click="() => {
                $router.push('/dictSPic/' + currDict);
              }"
            style="background-image: url('/img/activitytype/cards.png')"
          />
          <div
            class="lessoncard--simple choose--activity"
            @click="() => {
                $router.push('/dictSWheelPic/' + currDict);
              }"
            style="background-image: url('/img/activitytype/wheel.png')"
          />
          <div
            class="lessoncard--simple choose--activity"
            @click="() => {
                $router.push('/dictSWrite/' + currDict);
              }"
            style="background-image: url('/img/activitytype/alphabet.png')"
          />
          <div
            class="lessoncard--simple choose--activity"
            @click="() => {
                $router.push('/dictWrite/' + currDict);
              }"
            style="background-image: url('/img/activitytype/typing.png')"
          />
         
           
        </div>
      </div>
    </div>
    <!-- <img src="/img/level/starter.png"/> -->
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import BackButton from "@/UI/BackButton.vue";

export default defineComponent({
  setup() {},
  components: {
    BackButton,
  },
  async created() {
    // this.jdata = Array<IDictionary>(
    //   await import("@/data/sdict/" + "colorsCards.json")
    // );
    // import("@/data/sdict/" + "colorsCards.json")
    //   .then(module=>{console.log(Array<IDictionary>(module.default)) });
    //console.log(this.$route.params);
    import("@/data/module/hello.json").then((module) => {
      this.currModule = module.default;
    });

    //console.log(this.jdata);
    //this.json = this.jdata;
    //this.currObj=this.jdata[0];
    //this.currObj=(Array<IDictionary>)this.jdata[0];
  },
  data() {
    return {
      currModule: {} as IModule,
      isShowDialog: false,
      currDict: "",
    };
  },
  methods: {
    route(item: IModuleActivity) {
      if (!item.rUrl) return;
      //console.log(item.aType);
      //var rType: ActivityType = item.aType as ActivityType;
      //console.log(item.rUrl);

      if (item.aType == "UNIVERSAL") {
        this.isShowDialog = true;
        this.currDict = item.rUrl;

        return;
      }

      if (item.aType === "ListSwipe") {
        this.$router.push("/simpleDict/" + item.rUrl);
      }

      if (item.aType === "SimpleSelect") {
        this.$router.push("/dictSPic/" + item.rUrl);
      }

      if (item.aType === "CircleSelect") {
        this.$router.push("/dictSWheelPic/" + item.rUrl);
      }

      if (item.aType === "CloudSelect") {
        this.$router.push("/dictSPic/" + item.rUrl);
      }
    },
    getWaterMark(item: IModuleActivity) {
      if (item.aType === "ListSwipe") {
        //this.$router.push("/simpleDict/" + item.rUrl);
      }

      if (item.aType === "SimpleSelect") {
        return "/img/activitytype/quiz.png";
      }

      if (item.aType === "CircleSelect") {
        return "/img/activitytype/wheel.png";
      }

      if (item.aType === "CloudSelect") {
        return "/img/activitytype/cloud.png";
      }

      return "";
    },
    colorView() {
      console.log("ColorView");
      this.$router.push("/ColorView");
    },
  },
});
</script>

<style>
.starter {
  /* display: flex; */
  min-height: 100vh;

  /* background-color: aliceblue;
  background-image: url("@/assets/img/level/starter.png");
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  background-size: cover; */

  background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
}
#x {
  position: absolute;
  background: #8c21aa;
  color: white;
  top: -10px;
  right: -10px;
  width: 30px;
  height: 30px;
  border-radius: 15px;
}
.modal-bg {
  position: fixed;
  z-index: 8000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.9;
}

.modal {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.modal--content {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 90vw;
  min-height: 90vh;
  margin-top: 5vh;

  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 20px 18px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
}

.internal--level {
  padding: 50px;
}
</style>
