<template>
  <div class="scene">
    <div
      class="flex flex-row justify-between items-center"
      style="height: 10vh"
    >
      <BackButton />
      <div class="flex flex-row cent">
        <h1 class="head--page">{{ currDict.name }}</h1>
        <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
        <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
        <!-- <SoundButton :CaptionText="currDict.name" LinkSound="" />
         
        <SoundNativeButton :CaptionText="getNative" LinkSound="" />   -->
      </div>
      <p></p>
    </div>
    <div
      class="flex flex-col justify-between items-center"
      style="min-height: 90vh"
    >
      <div class="flex flex-row justify-center">
        <h1 class="text-6xl">
          {{ currObj.name }}
        </h1>
        <SoundButton :CaptionText="currObj.name" LinkSound="" />
        <SoundNativeButton
          v-if="currObj?.translates"
          :CaptionText="getNative"
          LinkSound=""
        />
      </div>

      <div class="flex flex-col h-65 justify-items-center items-center s w-1/3">
        <!-- <CardItemImg
          v-for="item in currAnswers"
          :key="item.id"
          :Caption="item.name"
          :BGColor="item.bg"
          :Translations="item.translates"
          :Img="item.image"
          :Answ="item.nameAnswer"
          :IsCorrect="item.name === currObj.name"
          @click="cardClick(item.name === currObj.name)"
        /> -->
        <CardItemImg
          :key="currObj.id"
          :Caption="currObj.name"
          :BGColor="currObj.bg"
          :Translations="currObj.translates"
          :Img="currObj.image"
          :IsSelected="isSelected"
          :IsCorrect="isCorrect"
          class="m-5"
        />
        <div class="flex flex-row">
          <input
            id="search"
            class="w-full p-4 text-xl border focus:ring-4 uppercase font-bold"
            :readonly="true"
            ref="aInput"
            v-model="currWriting"
            :disabled="isSolved"
          />
          <button
            type="submit"
            @click="checkClick"
            :disabled="isSolved"
            class="text-white font-bold bg-green-700 hover:bg-green-800 focus:ring-4 focus:outline-none focus:ring-blue-300 rounded-lg text-sm px-4 py-2"
          >
            CHECK
          </button>
        </div>
        <div class="flex m-5">
          
          <CardLetterSel
            v-for="item in currLetters"
            :key="item.id"
            :Caption="item.name"
            :IsCorrect="item.name === nextLetter"
            @click="cardClick(item.name)"
          />
        </div>
      </div>
      <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
      <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
      <h2 v-if="isSolved">Good JOB</h2>
      <h2 v-else>Waiting for solve...</h2>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useSound } from "@vueuse/sound";
//import wrSound from "@/assets/sounds/wrong1.wav"
// import {cDicts} from '@/data/colorsCards.js'
//import json from "../data/colorsCards.json";
import CardItemImg from "@/components/cards/CardItemImg.vue";
import CardLetterSel from "@/components/cards/CardItemSelLetter.vue";
import SwitchDictButton from "@/UI/SwitchDictButton.vue";
import BackButton from "@/UI/BackButton.vue";
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";
//import { Howl, Howler } from "howler";

export default defineComponent({
  setup() {
    // import("@/data/sdict/" + "colorsCards.json")
    //   .then(module=>{console.log(module.default)});
    const wrongSound = useSound("/sound/common/wrong3.waw", { volume: 1 });
    const goodSound = useSound("sound/common/success2.waw", { volume: 1 });
    const audioW = ref<HTMLAudioElement>();
    const audioS = ref<HTMLAudioElement>();
    const aInput = ref<HTMLInputElement>();
    // var sound = new Howl({
    //   src: ["/sound/common/wrong1.waw"],
    // });

    // sound.play();
    //wrongSound.play();
    return { wrongSound, goodSound, audioW, audioS, aInput };
  },
  data() {
    return {
      currObj: {} as IDictionaryItem,
      currAnswers: [] as IDictionaryItem[],
      jdata: [] as IDictionaryItem[],
      currDict: {} as IDictionary,
      currLetters: [] as ILetter[],
      nextLetter: "",
      count: 0,
      isSolved: false,
      oldval: -100,
      currWriting: "",
      isSelected: false,
      isCorrect: false,
    };
  },
  computed: {
    getNative() {
      var locLang = "ru-RU";
      // console.log(this.currObj as IDictionaryItem);

      var translates = <ITranslations[]>this.currObj.translates;

      if (this.currObj?.translates) {
        //console.log(this.currObj.translates);
        var castedArr = <ITranslations[]>this.currObj.translates;
        //console.log(castedArr);
        var foundTransl = castedArr.filter((a) => a.lang === locLang);
        //console.log(foundTransl[0]);
        return foundTransl[0]?.name;
      } else {
        return "";
      }

      return "";
    },
    getNextTask(): string {
      var cardsCount = this.jdata.length;
      //console.log(cardsCount);
      var randVal = Math.floor(Math.random() * cardsCount);
      if (this.jdata.length > 0) {
        var qElement = this.jdata.length;

        //console.log(this.count);
        //console.log(randVal);
        return qElement.toString();
      } else {
        return "-1";
      }
    },
  },
  async created() {
    import("@/data/sdict/" + this.$route.params.dictname + ".json").then(
      (module) => {
        this.currDict = module.default;
        this.jdata = module.default.items;

        this.currObj = this.jdata[0];
        this.moveNext();
      }
    );
  },
  components: {
    CardItemImg,
    SwitchDictButton,
    BackButton,
    SoundButton,
    SoundNativeButton,
    CardLetterSel,
  },
  methods: {
    checkClick() {
      if (this.currWriting.toUpperCase() === this.currObj.name.toUpperCase()) {
        this.isSelected = true;
        this.isCorrect = true;
        this.isSolved = true;
        this.audioS?.play();

        setTimeout(() => this.moveNext(), 2000);
      } else {
        this.isCorrect = false;
        this.audioW?.play();
        this.isSelected = true;
      }
    },
    cardClick(b: string) {

      if(b===this.nextLetter){
      this.currWriting=this.currWriting+b;
      var diff = this.currObj.name
        .toUpperCase()
        .replace(this.currWriting.toUpperCase(), "");
        console.log(diff);
        if(diff.length>0){
          this.nextLetter=diff[0];
        }
      }
    },
    moveNext() {
      this.currWriting = "";
      this.isSelected = false;
      this.isCorrect = false;
      // this.$refs.aInput.$el.focus();
      //this.aInput?.value.focus();

      //@ts-ignore
      // this.aInput.focus();

      //this.$nextTick(() => this.aInput?.focus())
      var randVal = Math.floor(Math.random() * this.jdata.length);

      while (randVal === this.oldval) {
        randVal = Math.floor(Math.random() * this.jdata.length);
      }
      this.currObj = this.jdata[randVal];

      var tLetters: string[] = this.currObj.name.toUpperCase().split("");
      //console.log(tLetters);

      var t = tLetters
        .sort(function () {
          return 0.7 - Math.random();
        })
        .map((item) => {
          return { id: Math.floor(Math.random() * 100000), name: item };
        });
      //console.log(t);
      this.currLetters = t;
      this.nextLetter=this.currObj.name.toUpperCase()[0];
      //We need map to force refresh same cards
      // this.currAnswers = answers
      //   .sort(function () {
      //     return 0.5 - Math.random();
      //   })
      //   .map((item) => {
      //     return { ...item, id: item.id + Math.floor(Math.random() * 100000) };
      //   });

      this.isSolved = false;
    },
  },
});
</script>

<style scoped>
.scene {
  /* display: flex;
  height: 100vh; */
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  /* background-color: gray; */
}
</style>
