  /* border: 2px violet solid; */
  /* transform: translateY(-5px); */
  /* filter: drop-shadow(15px 15px 15px #a809cc); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
   
  justify-content: center;
   
  /* width: 200px;
  height: 200px; */
   
  
}
.card--correct {
  border: 5px rgb(11, 182, 76) solid;
  filter: drop-shadow(15px 15px 15px rgb(11, 182, 76));
}
.card--wrong {
  border: 5px rgb(182, 11, 11) solid;
  filter: drop-shadow(15px 15px 15px rgb(182, 11, 11));
  /* opacity: 0.3; */
}

.card--sm--img img {
  object-fit: cover;
  object-position: center;
}
